import Phaser from 'phaser'

export default class IntroScene extends Phaser.Scene {
  constructor () {
    super('IntroScene');
  }

  preload () {
    
  }

  create() {
    const forest = this.add.graphics({ fillStyle: { color: 0x00ff00, alpha: 1 } })
    forest.fillRect(0, 0, this.renderer.width * 2, this.renderer.height)
    const forestText = this.add.text(80, 10, "forest",
      {
        fontFamily: 'GameGirl',
        fontSize: "10px",
        fill: "#ffffff",
      }
    )
    const container = this.add.container()
    container.add(forest)
    container.add(forestText)
    const ground = this.add.graphics({ fillStyle: { color: 0x654321, alpha: 1 } })
    ground.fillRect(0, this.renderer.height - this.renderer.height * 0.3, this.renderer.width * 2, this.renderer.height * 0.3)
    this.add.text(80, this.renderer.height - this.renderer.height * 0.3 + 10, "ground",
      {
        fontFamily: 'GameGirl',
        fontSize: "10px",
        fill: "#ffffff",
      }
    )

    const dark = this.add.graphics({ fillStyle: { color: 0x000000, alpha: 1 } })
    dark.fillRect(this.renderer.width + 100, this.renderer.height, 200, this.renderer.height * 0.3)
    dark.name = 'dark'

    const player = this.add.graphics({ fillStyle: { color: 0xb75331, alpha: 1 } })
    player.fillRect(this.renderer.width, this.renderer.height - this.renderer.height * 0.3 - 50, 50, 50)
    player.name = 'player'

    const partner = this.add.graphics({ fillStyle: { color: 0xdab263, alpha: 1 } })
    partner.fillRect(this.renderer.width + 200, this.renderer.height - this.renderer.height * 0.3 - 50, 50, 50)
    partner.name = 'partner'
    
    this.cameras.main.fadeIn(1000, 0, 0, 0)
    this.cameras.main.once('camerafadeincomplete', this.animateIntro.bind(this))
  }

  animateIntro() {

    this.cameras.main.pan(this.renderer.width, this.renderer.height/2, 2000, 'Sine.easeInOut');
    this.cameras.main.once(Phaser.Cameras.Scene2D.Events.PAN_COMPLETE, () => {
      this.cameras.main.shake(2000, 0.02)
      this.tweens.add({
        targets: this.children.getByName('dark'),
        y: -this.renderer.height * 0.3,
        duration: 2000
      })
      this.tweens.add({
        targets: this.children.getByName('partner'),
        y: this.renderer.height,
        delay: 2000,
        duration: 2000,
        ease: 'Sine.in'
      })
      this.tweens.add({
        targets: this.children.getByName('player'),
        y: this.renderer.height,
        x: {
          value: this.children.getByName('player').x + 150,
          duration: 1000,
          ease: 'Cubic.easeOut',
        },
        delay: 4000,
        duration: 2000,
        ease: 'Back.easeInOut',
        onComplete: () => {
          this.cameras.main.fade(1000, 0, 0, 0)
          this.cameras.main.once('camerafadeoutcomplete', () => void this.scene.start('GameScene', { level: 1}))
        }
      })

    })
  }
}
