import LevelBase from './levelBase'

class Level1 extends LevelBase {
  constructor() {
    super()
    this.id = 'level_1'
    this.name = 'The beginning'
  }

  connectToScene(scene) {
    super.connectToScene(scene)
    scene.cameras.main.once('camerafadeincomplete', () => {
      alert('INTRO MESSAGE, "Guess I can move those boulders"')
    })
  }

  onInteractWith(el) {
    if(el.position.x === 18 && el.position.y === 14) {
      alert('>>> Level 1 completed <<<')
      this.onComplete()
    }
  }
}

export default new Level1()
