import Phaser from 'phaser'
import { TICK_TIME, PLAYER_TICK_TIME, PIXEL_NAME, DIRS, PIXEL_TO_TILEMAP_POSITION } from "../../utils/constants"
import {
  Player,
  Boulder,
  Wall,
  Empty,
  Ground,
  Barrel,
  Enemy,
  Interactive,
  Jewel,
  Explosion,
} from '../objects'

class Level {
  constructor(config) {
    this.config = config
    this.moveables = []
    this.objects = []
    this.scene = null
    this.player = null
    this.playerTimeoutId = 0
    this.isPaused = false
  }

  connectToScene(phaserScene) {
    this.scene = phaserScene
    const layer = this.scene.children.getByName('layer_1')
    const { tilemap: map } = layer
    

    this.objects = this.config.map((r, y) => r.map((c, x) => {
      // factory
      let element
      switch(c) {
        case PIXEL_NAME.WALL:
          element = new Wall(c, this)
          element.setPosition({ x, y })
          break
        case PIXEL_NAME.GROUND:
          element = new Ground(c, this)
          element.setPosition({ x, y })
          break
        case PIXEL_NAME.BOULDER:
          const boulder = this.scene.add.sprite(48, 16).setOrigin(0, 0.25).play('boulder_idle')
          boulder.setScale(layer.scaleX)
          boulder.x = map.tileToWorldX(x)
          boulder.y = map.tileToWorldY(y)
          
          element = new Boulder(c, this)
          element.alien = boulder
          break
        case PIXEL_NAME.JEWEL:
          const jewel = this.scene.add.graphics({ fillStyle: { color: 0xffff00, alpha: 1 } })
          jewel.setScale(layer.scaleX)
          // jewel.fillRect(0, 0, map.tileWidth * layer.scaleX, map.tileHeight * layer.scaleY)
          jewel.fillRect(0, 0, map.tileWidth, map.tileHeight)

          jewel.x = map.tileToWorldX(x)
          jewel.y = map.tileToWorldY(y)
          
          element = new Jewel(c, this)
          element.alien = jewel
          break
        case PIXEL_NAME.BARREL:
          const barrel = this.scene.add.graphics({ fillStyle: { color: 0x663300, alpha: 1 } })
          barrel.setScale(layer.scaleX)
          // barrel.fillRect(0, 0, map.tileWidth * layer.scaleX, map.tileHeight * layer.scaleY)
          barrel.fillRect(0, 0, map.tileWidth, map.tileHeight)

          barrel.x = map.tileToWorldX(x)
          barrel.y = map.tileToWorldY(y)
          
          element = new Barrel(c, this)
          element.alien = barrel
          break
        case PIXEL_NAME.PLAYER:
          const player = this.scene.add.sprite(48, 16).setOrigin(0, 0.25).play('brave_idle')
          player.setScale(layer.scaleX)
          player.x = map.tileToWorldX(x)
          player.y = map.tileToWorldY(y)

          player.name = 'brave'
          player.on(Phaser.Animations.Events.ANIMATION_COMPLETE, () => {
            if(!player.isMoving) {
              // player.play('brave_idle')
            }
          })

          element = new Player(c, this)
          element.alien = player

          // add shortcut reference
          this.player = element
          break
        case PIXEL_NAME.ENEMY:
          const enemy = this.scene.add.graphics({ fillStyle: { color: 0xff0000, alpha: 1 } })
          enemy.fillRect(0, 0, map.tileWidth, map.tileHeight)
          enemy.setScale(layer.scaleX)
          this.scene.addToScalables(enemy, { x, y })
          enemy.x = map.tileToWorldX(x)
          enemy.y = map.tileToWorldY(y)
          element = new Enemy(c, this)
          element.alien = enemy
          break
        case PIXEL_NAME.INTERACTION:
          element = new Interactive(c, this)
          break
        default:
          element = new Empty(c, this)

      }
      element.setPosition({ x, y })
      if (!this.objectIsEmpty(element) && !element.isRigid) {
        // enters backward to start evaluating lower elements into gravityLoop
        // this.moveables.unshift(element)
        this.moveables.push(element)
      }
      this.onAddElement(element)
      return element
    }))
  }

  gravityLoop() {
    if(this.isPaused) { return }
    this.moveables.sort((a, b) => a.position.y - b.position.y).forEach(el => {
      const canFallDown = el.canFallDown()
      const canFallLeft = el.canFallLeft()
      const canFallRight = el.canFallRight()
      let nextPos
      if(canFallDown) {
        nextPos = { x: el.position.x, y: el.position.y + 1 }
        el.isFalling++
      }
      if (!canFallDown && canFallLeft) {
        nextPos = { x: el.position.x - 1, y: el.position.y}
        if(el.isFalling) {
          nextPos.y ++
        }
        el.isFalling++
      }
      if (!canFallDown && !canFallLeft && canFallRight) {
        nextPos = { x: el.position.x + 1, y: el.position.y}
        if(el.isFalling) {
          nextPos.y ++
        }
        el.isFalling++
      }

      if (nextPos) {
        this.moveObject(el, nextPos)
      } else {
        if(el.isFalling && el.constructor.name === Barrel.name) {
          el.explode()
        }
        el.isFalling = 0
      }
    })

    this.enemiesLoop()
  }
  enemiesLoop() {

    if(this.isPaused) { return }
    let nextPos, neighbour, isDestinationEmpty, isPerpToAnchor, newAnchor, nextDir, backToTrackDir, nextAnchorPos
    
    const helperDirCalc = (enemy, nextPos, nextDir, backToTrackDir, nextAnchorPos) => {
      neighbour = this.getObjectAt(nextPos.x, nextPos.y)
      isDestinationEmpty = this.objectIsEmpty(neighbour) || neighbour?.constructor?.name === Player.name // this.isDestinationEmpty(nextPos.x, nextPos.y)
      if (!isDestinationEmpty && !enemy.anchorPosition) {
        enemy.anchorPosition = neighbour.position
        // counter clockwise
        enemy.moveIntentDir = nextDir
      }
      if (isDestinationEmpty && neighbour.constructor.name === Player.name) {
        this.moveObject(enemy, nextPos)
      }
      if (isDestinationEmpty) {
        if (!enemy.anchorPosition) {
          enemy.moveIntentDir = nextDir
          return
        }
        isPerpToAnchor = enemy.position.x === enemy.anchorPosition.x || enemy.position.y === enemy.anchorPosition.y
        // scenario 1: enemy is perpendicular to anchor
        if (isPerpToAnchor) {
          // updates anchor counter clockwise
          newAnchor = this.getObjectAt(nextAnchorPos.x, nextAnchorPos.y)
          if (!this.objectIsEmpty(newAnchor) && newAnchor?.constructor?.name !== Player.name) {
            enemy.anchorPosition = newAnchor.position
          } else {
            enemy.moveIntentDir = backToTrackDir
          }
        }
        this.moveObject(enemy, nextPos)
      } else {
        enemy.anchorPosition = neighbour.position
        enemy.moveIntentDir = nextDir
      }
    }
    this.moveables
    .filter(el => el.constructor.name === Enemy.name)
    .forEach(enemy => {
      // potential path finding mechanism @see https://github.com/qiao/PathFinding.js
      // if(!enemy.path) {
      //   const grid = new window.PF.Grid(this.objects.map(row => row.map(el => el.isEmpty ? 0 : 1)));
      //   enemy.pathfinder = new window.PF.AStarFinder();
      //   const path = enemy.pathfinder.findPath(enemy.position.x, enemy.position.y, thi.player.position.x, this.player.position.y, grid);
      // }
      switch(enemy.moveIntentDir) {
        case DIRS.UP:
          nextPos = { x: enemy.position.x, y: enemy.position.y - 1}
          nextDir = DIRS.LEFT
          backToTrackDir = DIRS.RIGHT
          nextAnchorPos = { x: nextPos.x + 1, y: nextPos.y }
          helperDirCalc(enemy, nextPos, nextDir, backToTrackDir, nextAnchorPos)
          
          break
        case DIRS.RIGHT:
          nextPos = { x: enemy.position.x + 1, y: enemy.position.y}
          nextDir = DIRS.UP
          backToTrackDir = DIRS.DOWN
          nextAnchorPos = { x: nextPos.x, y: nextPos.y + 1 }
          helperDirCalc(enemy, nextPos, nextDir, backToTrackDir, nextAnchorPos)
          
          break
        case DIRS.DOWN:
          nextPos = { x: enemy.position.x, y: enemy.position.y + 1}
          nextDir = DIRS.RIGHT
          backToTrackDir = DIRS.LEFT
          nextAnchorPos = { x: nextPos.x - 1, y: nextPos.y }
          helperDirCalc(enemy, nextPos, nextDir, backToTrackDir, nextAnchorPos)
          
          break
        case DIRS.LEFT:
          nextPos = { x: enemy.position.x - 1, y: enemy.position.y}
          nextDir = DIRS.DOWN
          backToTrackDir = DIRS.UP
          nextAnchorPos = { x: nextPos.x, y: nextPos.y - 1 }
          helperDirCalc(enemy, nextPos, nextDir, backToTrackDir, nextAnchorPos)
          
          break

        default:
      }
    })
  }
  playerLoop() {
    if(this.isPaused || this.player.inactive) { return }
    clearTimeout(this.playerTimeoutId)
    const player = this.player
    
    const nextPos = { x: player.position.x, y: player.position.y }
    switch(player.moveIntentDir) {
      case DIRS.UP:
        if(player.canMoveUp()) nextPos.y -= 1
        break
      case DIRS.DOWN:
        if(player.canMoveDown()) nextPos.y += 1
        break
      case DIRS.LEFT:
        if(player.canMoveLeft()) nextPos.x -= 1
        break
      case DIRS.RIGHT:
        if(player.canMoveRight()) nextPos.x += 1
        break
      default:
    }
    if ((player.position.x !== nextPos.x || player.position.y !== nextPos.y) && !player.isMoving) {
      player.isMoving = true
      if (player.position.x < nextPos.x) {
        player.alien.setOrigin(0, 0.25)
        player.alien.setFlipX(false)
      }
      if (player.position.x > nextPos.x) {
        player.alien.setFlipX(true)
        player.alien.setOrigin(0.25, 0.25)
      }
      player.alien.emit('player_moves', nextPos)
      this.moveObject(player, nextPos)
    }

    this.playerTimeoutId = setTimeout(() => {
      player.isMoving = false
      player.alien.play('brave_idle')
      if(player.moveIntentDir !== '') {
        this.playerLoop()
      }
    }, PLAYER_TICK_TIME);
  }
  // afterPlayerLoop(cb) {
  //   this.afterPlayerLoopCallbacks.push(cb)
  // }
  moveObject(el, pos) {
    const layer = this.scene.children.getByName('layer_1')
    const { tilemap: map } = layer
    const origin = { x: el.position.x, y: el.position.y }
    // assume destination is occupeable
    const destinationElement = this.getObjectAt(pos.x, pos.y)
    if (!this.objectIsEmpty(destinationElement) || destinationElement.constructor.name === Interactive.name) {
      destinationElement.overrunBy(el)
      el.runningOver(destinationElement)
    }

    if(el.inactive) return

    // clear origin position
    this.setObjectAt(origin.x, origin.y, new Empty(PIXEL_NAME.EMPTY, this))
    // fill destination position
    this.setObjectAt(pos.x, pos.y, el)
    
    // updates element position
    el.move(pos)

    if(el.constructor.name === Player.name) {
      // alien anim
      this.scene.tweens.add({
        targets: el.alien,
        x: {
          value: map.tileToWorldX(pos.x),
          duration: PLAYER_TICK_TIME,
        },
        y: {
          value: map.tileToWorldY(pos.y),
          duration: PLAYER_TICK_TIME,
        },
      });
      el.alien.play('brave_run')
    }
    if(el.constructor.name === Enemy.name) {
      // alien anim
      this.scene.tweens.add({
        targets: el.alien,
        x: {
          value: map.tileToWorldX(pos.x),
          duration: TICK_TIME,
        },
        y: {
          value: map.tileToWorldY(pos.y),
          duration: TICK_TIME,
        },
      });
    }
    if(el.constructor.name === Jewel.name) {
      // alien anim
      this.scene.tweens.add({
        targets: el.alien,
        x: {
          value: map.tileToWorldX(pos.x),
          duration: TICK_TIME * 0.5,
        },
        y: {
          value: map.tileToWorldY(pos.y),
          delay: pos.x !== origin.x ? TICK_TIME * 0.5 : 0,
          duration: pos.x !== origin.x ? TICK_TIME * 0.5 : TICK_TIME,
        },
        // duration: TICK_TIME,
      });
    }
    if(el.constructor.name === Barrel.name) {
      // alien anim
      this.scene.tweens.add({
        targets: el.alien,
        x: {
          value: map.tileToWorldX(pos.x),
          duration: TICK_TIME * 0.5,
        },
        y: {
          value: map.tileToWorldY(pos.y),
          delay: pos.x !== origin.x ? TICK_TIME * 0.5 : 0,
          duration: pos.x !== origin.x ? TICK_TIME * 0.5 : TICK_TIME,
        },
        // duration: TICK_TIME,
      });
    }
    if(el.constructor.name === Boulder.name) {
      // alien anim
      this.scene.tweens.add({
        targets: el.alien,
        x: {
          value: map.tileToWorldX(pos.x),
          duration: TICK_TIME * 0.5,
        },
        y: {
          value: map.tileToWorldY(pos.y),
          delay: pos.x !== origin.x ? TICK_TIME * 0.5 : 0,
          duration: pos.x !== origin.x ? TICK_TIME * 0.5 : TICK_TIME,
        },
        // duration: TICK_TIME,
      });
      if (pos.x !== origin.x) {
        el.alien.play(pos.x > origin.x ? 'boulder_roll_right' : 'boulder_roll_left')
      }
    }
  }
  pause() {
    this.isPaused = true
  }
  resume() {
    this.isPaused = false
  }
  onCharacterDie(el) {
    this.setObjectAt(el.position.x, el.position.y,  new Empty(PIXEL_NAME.EMPTY, this))
    this.moveables = this.moveables.filter(e => e !== el)
    if (el.constructor.name === Player.name) {
      // this.player = null
    }
  }
  onJewelPick(el) {
    this.setObjectAt(el.position.x, el.position.y,  new Empty(PIXEL_NAME.EMPTY, this))
    this.moveables = this.moveables.filter(e => e !== el)
  }
  onDestroy(el) {
    if(el.constructor.name === Boulder.name) {
      // console.log(el);
    }
    this.setObjectAt(el.position.x, el.position.y,  new Empty(PIXEL_NAME.EMPTY, this))
    this.moveables = this.moveables.filter(e => e !== el)
  }
  onExplode(el) {
    this.setObjectAt(el.position.x, el.position.y,  new Empty(PIXEL_NAME.EMPTY, this))
    this.moveables = this.moveables.filter(e => e !== el)
    for(let x = el.position.x - 2; x <= el.position.x + 2; x++) {
      for(let y = el.position.y - 2; y < el.position.y + 2; y++) {
        const el = this.getObjectAt(x, y)
        const explosion = new Explosion(PIXEL_NAME.EXPLOSION, this)
        if(el.isBreakable || this.objectIsEmpty(el)) {
          explosion.setPosition(x, y)
          this.objects[y][x] = explosion
        }
        if (el && !this.objectIsEmpty(el)){
          el.overrunBy(explosion)
        }
      }
    }
  }
  onComplete() {
    this.objects.forEach(row => row.forEach(el => {
      if(el !== this.player && el.alien) {
        el.alien.destroy()
      }
    }))
    this.pause()
    this.scene.events.emit('levelcompleted', this)
    // this.moveables = []
    // this.objects.map(el => el.alien?.destroy())
  }
  onInteractWith(el) {
    console.warn(`implement interaction for ${this.id}`)
  }
  onAddElement(el) {
    console.warn(`implement element added ${this.id}`)
  }
  getPlayerPos() { return this.player.position}
  getConfigAt(x, y) {
    return this.isDestinationInBounds(x, y) && this.config[y][x]
  }
  getCoords(id) {
    const found = []
    for(let i = 0; i < this.config.length; i++) {
      for(let j = 0; j < this.config[i].length; j++) {
        if (this.config[i][j] === id) {
          found.push({x: j, y: i})
        }
      }
    }
    return found
  }
  getObjectAt(x, y) {
    return this.isDestinationInBounds(x, y) && this.objects[y][x]
  }
  setObjectAt(x, y, obj) {
    this.objects[y][x] = obj
  }
  isPositionClear(x, y) {
    return this.getObjectAt(x, y).isEmpty
  }
  isDestinationInBounds(x, y) {
    const xInBounds = x < this.config[0].length && x >= 0
    const yInBounds = y < this.config.length && y >= 0
    return xInBounds && yInBounds
  }
  objectIsEmpty(el) {
    return el.isEmpty
  }
  isDestinationEmpty(x, y) {
    return this.getObjectAt(x, y).isEmpty
  }
}

export default Level
