import Phaser from 'phaser'
import { PIXEL_NAME, PIXEL_TO_TILEMAP_POSITION } from '../utils/constants'
import Level1 from './levels/level_1'
import Level2 from './levels/level_2'

const LEVELS = [Level1, Level2]

export default class LevelManager {
  constructor(scene, currentLevel = 0) {
    this.scene = scene
    this.currentIndex = currentLevel - 1
    this.current = LEVELS[this.currentIndex]
    this.start()
  }
  
  start() {
    this.current = LEVELS[this.currentIndex]
    this.current.config = this.generateConfig()
    this.map = this.createMap()
    this.current.connectToScene(this.scene)
  }

  next() {
    // destroy and unload
    this.currentIndex += 1
    if (LEVELS.length[this.currentIndex]) {
      this.start()
    } else {
      console.log(this.scene)
      this.scene.scene.start('MenuScene')
    }

  }

  generateConfig() {
    const scene = this.scene
    const levelId = this.current.id
    var texture = scene.textures.get(levelId);
    var image = texture.getSourceImage();
    const levelConfig = Array(image.height).fill('000000').map(r => Array(image.width).fill('000000'))
    for(let j = 0; j < image.width; j++) {
      for(let i = 0; i < image.height; i++) {
        const pixColor = scene.textures.getPixel(j, i, levelId)
        const key = `${Phaser.Display.Color.ComponentToHex(pixColor.r)}${Phaser.Display.Color.ComponentToHex(pixColor.g)}${Phaser.Display.Color.ComponentToHex(pixColor.b)}`
        levelConfig[i][j] = key
      }
    }
    return levelConfig
  }

  createMap() {
    const scene = this.scene
    const levelData = this.current
    const SCALE = scene.data.get('custom_scale')
    const camera = this.scene.cameras.main

    let map = this.map
    if(map && map.scene) {
      map.destroy()
    }

    // Creating a blank tilemap with dimensions matching the dungeon
    map = scene.make.tilemap({ tileWidth: 20, tileHeight: 20, width: levelData.config[0].length, height: levelData.config.length });

    // addTilesetImage: function (tilesetName, key, tileWidth, tileHeight, tileMargin, tileSpacing, gid)
    let tileset = map.addTilesetImage('tiles', 'main-tiles', 20, 20, 1, 2);

    let layer = map.createBlankLayer('layer_1', tileset);
    layer.name = 'layer_1'
    layer.depth = 0

    layer.setScale(SCALE)

    // Fill with black tiles
    layer.fill(1)

    levelData.config.forEach((row, x) => {
      row.forEach((el, y) => {
        const tile = levelData.config[x][y]
        if (![PIXEL_NAME.EMPTY, PIXEL_NAME.PLAYER, PIXEL_NAME.BOULDER, PIXEL_NAME.INTERACTION].includes(tile)) {
          if (tile === PIXEL_NAME.WALL) {
            const tile = PIXEL_TO_TILEMAP_POSITION.calculateWallTileWithNeighbours(x, y, levelData.config)
            map.putTileAt(tile, y, x)
          } else {
            map.putTileAt(PIXEL_TO_TILEMAP_POSITION[tile], y, x)
          }
        } else {
          map.putTileAt(PIXEL_TO_TILEMAP_POSITION[PIXEL_NAME.EMPTY], y, x)
        }
      })
    })

    // set up camera
    camera.setBounds(0, 0, layer.width * layer.scaleX, layer.height * layer.scaleY);
    camera.zoom = 1

    return map
  }
}