import Phaser from 'phaser'
import { TICK_TIME } from '../utils/constants'

export default class Rules {
  constructor(scene) {
    this.scene = scene
    // gravity loop
    this.timer = 0
    this.worldTickFreq = TICK_TIME
    scene.events.addListener(Phaser.Scenes.Events.UPDATE, (...props) => void this.onSceneUpdate(...props))

    this.setUpPuseResumeGame()
    this.setUpCameraFollowPlayer()
    scene.events.on('levelcompleted', () => {
      scene.cameras.main.fade(1000, 0, 0)
      scene.cameras.main.once('camerafadeoutcomplete', () => {
        this.scene.levels.next()
        scene.cameras.main.fadeIn(1000, 0, 0)
      })
    })
  }

  setUpCameraFollowPlayer() {
    this.scene.events.addListener(Phaser.Scenes.Events.UPDATE, () => {
      const cam = this.scene.cameras.main
      const layer = this.scene.children.getByName('layer_1')
      const { tilemap: map } = layer
      const {x, y} = this.scene.levels.current.getPlayerPos()
      if (cam && map) {
        var smoothFactor = 0.9;
  
        cam.scrollX = smoothFactor * cam.scrollX + (1 - smoothFactor) * (map.tileToWorldX(x) - cam.width * 0.5);
        cam.scrollY = smoothFactor * cam.scrollY + (1 - smoothFactor) * (map.tileToWorldY(y) - cam.height * 0.5);
      }
    })
  }

  setUpPuseResumeGame() {
    const pauseGame = () => {
      this.scene.levels.current.pause()
      this.scene.events.emit('game_paused')
    }
    const resumeGame = () => {
      this.scene.levels.current.resume()
      this.scene.events.emit('game_resumed')
    }
    this.scene.input.keyboard.on('keydown', event => {
      if(event.key === 'Escape') {
        const action = this.scene.levels.current.isPaused ? resumeGame : pauseGame
        action()
      }
    })
  }

  onSceneUpdate(time, delta) {
    this.timer += delta
    if (this.timer >= this.worldTickFreq) {
      while (this.timer > this.worldTickFreq) {
          this.timer -= this.worldTickFreq
      }
      this.scene.levels.current.gravityLoop()
    }
  }
}
