export const TICK_TIME = 250
export const PLAYER_TICK_TIME = TICK_TIME - 25
export const TILE_SIZE = 20
export const TILES_BY_SCREEN = 20

export const PIXEL_NAME = {
  EMPTY: '000000',
  PLAYER: 'ff00ff',
  GROUND: '663300',
  BOULDER: '999999',
  WALL: '0000ff',
  ENEMY: 'ff0000',
  JEWEL: 'ffff00',
  BARREL: '00ffff',
  EXPLOSION: 'ffffff',
  INTERACTION: '00ff00'
}

export const DIRS = {
  DOWN: 'ArrowDown',
  UP: 'ArrowUp',
  LEFT: 'ArrowLeft',
  RIGHT: 'ArrowRight',
}

export const PIXEL_TO_TILEMAP_POSITION = {
  [PIXEL_NAME.EMPTY]: 0,
  [PIXEL_NAME.WALL]: 1,
  [PIXEL_NAME.GROUND]: 2,
  calculateWallTileWithNeighbours: (x, y, data) => {
    const W = (data[x]||[])[y-1] === PIXEL_NAME.WALL ? 'W' : '-'
    const N = (data[x-1]||[])[y] === PIXEL_NAME.WALL ? 'N' : '-'
    const E = (data[x]||[])[y+1] === PIXEL_NAME.WALL ? 'E' : '-'
    const S = (data[x+1]||[])[y] === PIXEL_NAME.WALL ? 'S' : '-'
    switch(`${N}${E}${S}${W}`) {
      case '----':
        default:
        return 11
      case 'NESW':
        return 12
      case 'N---':
        return 13
      case '-E--':
        return 14
      case '--S-':
        return 15
      case '---W':
        return 16
      case 'NE--':
        return 17
      case '-ES-':
        return 18
      case '--SW':
        return 19
      case 'N--W':
        return 20
      case 'NES-':
        return 22
      case '-ESW':
        return 23
      case 'N-SW':
        return 24
      case 'NE-W':
        return 25
      case 'N-S-':
        return 27
      case '-E-W':
        return 26
    }
  }
}