import Character from './Character'
import Enemy from './Enemy'
import { DIRS } from '../../utils/constants'

export default class Player extends Character {
  constructor(id, level) {
    super(id, level)
    this.power = 0.5
    this.moveIntentDir = ''

    const scene = level.scene
    scene.input.keyboard.on('keydown', event => {
      if(!event.repeat && [DIRS.UP, DIRS.DOWN, DIRS.LEFT, DIRS.RIGHT].includes(event.key)) {
        this.moveIntentDir = event.key;
        // calls loop to avoid delay between key press and visual movement at the begining of next loop
        if(!this.isMoving) {
          this.level.playerLoop()
        }
      }
    });

    scene.input.keyboard.on('keyup', event => {
      if (this.moveIntentDir === event.key) {
        this.moveIntentDir = ''
        this.pushingDirection = null
      }
    });
  }
  destroy() {
    const scene = this.level.scene
    // animate die
    scene.input.keyboard.off('keydown')
    scene.input.keyboard.off('keyup')
  }

  die() {
    this.inactive = true
    this.alien.alpha = 0.5
    this.destroy()
  }

  overrunBy(el) {
    this.die()
  }
  runningOver(el) {
    if(el.constructor.name === Enemy.name) {
      this.die()
    }
  }
}
