import LevelBase from './levelBase'
import { Interactive } from '../objects'

class Level2 extends LevelBase {
  constructor() {
    super()
    this.id = 'level_2'
    this.name = 'The finding'
  }

  // connectToScene(scene) {
  //   super.connectToScene(scene)
  //   scene.cameras.main.once('camerafadeincomplete', () => {
  //     alert('INTRO MESSAGE, "Guess I can move those boulders"')
  //   })
  // }

  onInteractWith(el) {
    if(el.position.x === 6 && el.position.y === 6) {
      this.player.moveIntentDir = ''
      alert('FIRST CLUE, "Hmm, these walls looks different. Like someone have put them here."')
    }
    if(el.position.x === 5 && el.position.y === 12) {
      const layer = this.scene.children.getByName('layer_1')
      const { tilemap: map } = layer
      this.player.moveIntentDir = ''
      alert('FIRST TOOL, "Great! Now, with this pickaxe I can break all those clods"')
      this.player.power = 1
      map.putTileAt(19, el.position.x, el.position.y)
    }
    if(el.position.x === 19 && el.position.y === 1) {
      alert('>>> Level 2 completed <<<')
      this.onComplete()
    }
  }

  onAddElement(el) {
    if(el.constructor.name === Interactive.name && el?.position.x === 5 && el?.position.y === 12) {
      const layer = this.scene.children.getByName('layer_1')
      const { tilemap: map } = layer
      map.putTileAt(54, el.position.x, el.position.y)
    }
  }
}

export default new Level2()
