import Fallable from './Fallable'
import Player from './Player'

export default class Jewel extends Fallable {
  constructor(id, level) {
    super({
      id,
      isRigid: false,
      isBreakable: true,
      isFlat: false,
      isPickable: true,
      isCrushable: false,
      level
    })
    this.strength = 4
  }
  overrunBy(el) {
    if(el.constructor.name === Player.name) {
      this.alien.visible = false
      // this.alien.alpha = 0.5
      this.level.onJewelPick(this)
    } else {
      super.overrunBy(el)
    }
  }
}
