import Fallable from './Fallable'

export default class Barrel extends Fallable {
  constructor(id, level) {
    super({
      id,
      isRigid: false,
      isBreakable: true,
      isFlat: true,
      isPickable: false,
      isCrushable: true,
      level
    })
  }
  explode() {
    // this.alien.alpha = 0.5
    this.alien.destroy()
    this.level.onExplode(this)
  }
  overrunBy(el) {
    this.explode()
  }
}
