import { PIXEL_TO_TILEMAP_POSITION, PIXEL_NAME } from '../../utils/constants'
import Destroyable from './Destroyable'

export default class Ground extends Destroyable {
  constructor(id, level) {
    super({
      id,
      isRigid: true,
      isBreakable: true,
      isFlat: true,
      isPickable: false,
      isCrushable: false,
      level
    })
    this.strength = 1
  }

  overrunBy(el) {
    super.overrunBy(el)
    // assume is player who overruns ground
    const layer = this.level.scene.children.getByName('layer_1')
    const { tilemap: map } = layer
    map.putTileAt(PIXEL_TO_TILEMAP_POSITION[PIXEL_NAME.EMPTY], this.position.x, this.position.y)
  }
}
