import GameObject from './GameObject'
import Empty from './Empty'
import { PIXEL_NAME, TICK_TIME } from '../../utils/constants'

export default class Explosion extends GameObject {
  constructor(id, level) {
    super({
      id,
      isRigid: false,
      isBreakable: true,
      isFlat: true,
      isPickable: false,
      isCrushable: true,
      level
    })
    this.power = 10
  }
  setPosition(x, y) {
    this.position = { x, y }
    
    const layer = this.level.scene.children.getByName('layer_1')
    const { tilemap: map } = layer
    const explosion = this.level.scene.add.graphics({ fillStyle: { color: 0xff0000, alpha: 1 } })
    explosion.fillRect(0, 0, map.tileWidth, map.tileHeight)
    explosion.setScale(layer.scaleX)

    explosion.x = map.tileToWorldX(x)
    explosion.y = map.tileToWorldY(y)

    this.level.scene.tweens.add({
      targets: explosion,
      alpha: 0,
      duration: TICK_TIME,
      onComplete: () => {
        explosion.destroy()
        this.level.objects[y][x] = new Empty(PIXEL_NAME.EMPTY, this.level)
      }
    });
    
    this.alien = explosion
    
  }
}
